import React from 'react';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';
import { Header } from 'components';
import { Layout, Container } from 'layouts';
import styled from '@emotion/styled';
import $ from 'jquery';

const divStyle = {
  margin: "1em",
  width: "100%",
  "textAlign": "left"
};

const FormDiv = styled.div` 
  @media (max-width: 650px)
  {
    width: 30%;
  }

  display: inline-block;
  width: 20%;
`;

const Button = styled.button `
  width: 14em;
  color: white;
  border: none;
  padding: 5px;
  &:hover {
    cursor: pointer;
    background-color: #0066ffe0;
    transition: ease-in-out .25s;
  }
  background-color: #002bffc9;
`;

const messageStyle = {
  display: 'none',
  'background-color': '#00e365b8',
};

const badMessageStyle = {
  display: 'none',
  'background-color': 'rgba(227, 0, 0, 0.72)',
};


const Contact = center => (
  <Layout>
    <Helmet title={'Contact Us'} />
    <Header title="Contact Us">Ask any questions or get in touch to negotiate a price! We will respond within a few hours depending on our workload.</Header>
    <Container center={center}>
      <h3>Please fill out the form below to contact us, or DM us on <a href="https://twitter.com/DestinyPinnacle">Twitter</a>.</h3>
        <div style={divStyle}>
          <FormDiv>
            <label htmlFor="pinnacleItem">Product: </label>
          </FormDiv>
          <FormDiv>
            <select id="pinnacleItem">
              <option value="other">Other</option>
              <option value="recluse">The Recluse</option>
              <option value="mountaintop">The Mountaintop</option>
              <option value="revoker">Revoker</option>
              <option value="lunas">Luna's Howl</option>
              <option value="lastword">Last Word</option>
              <option value="thorn">Thorn</option>
              <option value="lumina">Lumina</option>
              <option value="wendigo">Wendigo GL3</option>
              <option value="hush">Hush</option>
              <option value="oxygen">Oxygen SR3</option>
              <option value="breakneck">Breakneck</option>
              <option value="21delirium">21% Delirium</option>
              <option value="loadedquestion">Loaded Question</option>
              <option value="ironbanner">Iron Banner</option>
              <option value="gambit">Gambit Boost</option>
              <option value="glory">Glory Boost</option>
            </select>
          </FormDiv>
        </div>
        <div style={divStyle}>
          <FormDiv>
            <label htmlFor="discord">Discord ID: </label>
          </FormDiv>
          <FormDiv>
            <input id="discord" type="text" required />
          </FormDiv>
        </div>
        <div style={divStyle}>
          <FormDiv>
            <label htmlFor="email">Email: </label>
          </FormDiv>
          <FormDiv>
            <input id="email" type="text" required />
          </FormDiv>
        </div>
        <div style={divStyle}>
          <FormDiv>
            <label htmlFor="notes">Notes or questions: </label>
          </FormDiv>
          <FormDiv>
            <textarea id="notes" rows="4" cols="50" required />
          </FormDiv>
        </div>
        <div style={divStyle}>
          <Button onClick={SendEmail}>Send</Button>
        </div>
        <div id="message" style={messageStyle}></div>
        <div id="badMessage" style={badMessageStyle}></div>
    </Container>
    <Helmet>
      <script src="smtp.js"></script>
    </Helmet>
  </Layout>
);

function SendEmail(e) {
  e.preventDefault();
  let product = $('#pinnacleItem').val();
  let discord = $('#discord').val();
  let email = $('#email').val();
  let notes = $('#notes').val();
  let body = `
    Product: ${product}<br>
    Discord: ${discord}<br>
    Email: ${email}<br>
    Notes: ${notes}<br>
  `;

  if (discord == '' || discord.indexOf('#') == -1)
  {
    $('#badMessage').text('Please fill in your discord id correctly. It should contain a # symbol.').show();
    return;
  }

  if (email == '' || email.indexOf('@') == -1)
  {
    $('#badMessage').text('Please fill in your email correctly.').show();
    return;
  }

  if (notes == '')
  {
    $('#badMessage').text('Please fill in your questions or notes in the box above.').show();
    return;
  }

  $('#badMessage').text('').hide();
  Email.send({
   SecureToken: "13595f9d-6d10-47e0-a226-838682b19e4b",
    To : 'destinypinnacleservices@gmail.com',
    Subject : "Contact Form Filled Out",
    From: "destinypinnacleservices@gmail.com",
    Body: body
  }).then(
    message => {
      $('#message').text('Message sent successfully!');
      $('#message').show();
    }
  );
}

export default Contact;

Container.propTypes = {
  center: PropTypes.object,
};
